import {
    AUTHENTICATE_USER,LOGOUT_USER
} from './types';
import axios from 'axios';

export const AuthenticateUser = () => async dispatch => {
  
    const res = await axios.get('/ops/checklogin');
    //put usable data to the storage
    localStorage.setItem('hrcx_user', JSON.stringify(res.data));

    dispatch({
        type: AUTHENTICATE_USER,
        payload: res.data
    });
};
export const LogoutUser = () => async dispatch => {
    
    const res = await axios.get('/ops/logout');
    dispatch({
        type: LOGOUT_USER,
        payload: res.data
    });
};


