import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { Layout } from './components/Layout';
import { Home } from './components/Home';
import { Redirect } from './components/Redirect';
import Contacts from './components/contacts/Contacts';
import Contact from './components/contacts/Contact';
import { authenticationComp }    from './components/AuthenticatedComponent';
import AddContact from './components/contacts/AddContact';
import './sass/index.min.css';
import './sass/responsive/responsive.min.css'
import WebFont from 'webfontloader';

WebFont.load({
    google: {
        families: [
            'Titillium Web:300,400,700', 'sans-serif' && 'Poppins:300,400,600,700', 'sans-serif' && 'Open Sans:300,400,700', 'sans-serif' && 'Nunito:300,400,700', 'sans-serif'
        ]
    }
});

export default class App extends Component {
    static displayName = App.name;

    render() {
        return (
            <Layout>
                <Router>
                    <Switch>
                        <Route path="/(|/|home|)/" component={Home} />
                        <Route path="/Redirect" component={Redirect} />
                        <Route exact path="/contacts" component={Contacts} />
                        <Route exact path="/contact" component={Contact} />
                        <Route exact path="/contact/add" component={AddContact} />
                    </Switch>
                </Router>
            </Layout>
        );
    }
}



