import React, { Component } from 'react';


export class Filter extends Component {


    constructor(props) {
        super(props);
        let sortedData = []; 
        if (this.props.sortType === 'ascending') {
            sortedData = [...this.props.data].sort((a, b) => {
                if (a !== null && b !== null) {
                    if (typeof a[this.props.sortedLabel] === "string") {

                        return a[this.props.sortedLabel].localeCompare(b[this.props.sortedLabel])
                    } else {
                        return a[this.props.sortedLabel] - b[this.props.sortedLabel]
                    }
                }

            });
        } else {
            sortedData = [...this.props.data].sort((a, b) => {
                if (a !== null && b !== null) {
                    if (typeof a[this.props.sortedLabel] === "string") {

                        return b[this.props.sortedLabel].localeCompare(a[this.props.sortedLabel])
                    } else {
                        return b[this.props.sortedLabel] - a[this.props.sortedLabel]
                    }
                }
            });
        }
        console.log(sortedData)
        this.state = {
            data: this.props.data,
            filterText: '',
            filteredData: sortedData,
            columnNames: this.props.columnNames,
            filterColumnElements: [],
            activeColumn: '',
            toggle: false,
            filteredIndexes: [],
            newData: this.props.data,
            obj: null,
            sortType: this.props.sortType,
            sortedLabel: this.props.sortedLabel,
            actualString: this.props.actualString,

        };
        this.sendData(sortedData, 'sortedData')
        console.log(this.props.sortType, this.props.sortedLabel)
        this.setWrapperRef = this.setWrapperRef.bind(this);
        this.handleClickOutside = this.handleClickOutside.bind(this);
    }
    componentDidMount() {
        document.addEventListener('mousedown', this.handleClickOutside);
    }

    componentWillUnmount() {
        document.removeEventListener('mousedown', this.handleClickOutside);
    }
    setWrapperRef(node) {
        this.wrapperRef = node;   
    }
    handleClickOutside(event) {
        if (this.wrapperRef && !this.wrapperRef.contains(event.target)) {
            this.setState({
                toggle:false
            })
        }
    }
    componentDidUpdate = (prevProps) => {
        if (this.props.columnNames !== prevProps.columnNames || this.props.sortType !== prevProps.sortType || this.props.sortedLabel !== prevProps.sortedLabel) { 
            let sortedData = []       
            var temp = []
            for (var i = 0, j = 0; j < this.props.data.length; j++) {
                if (this.props.data[j] !== null) {
                    temp[i] = this.props.data[j]
                    i++

                }
            }
            if (this.props.sortType === 'ascending') {
                sortedData = [...this.state.filteredData].sort((a, b) => {
                    if (a !== null && b !== null) {
                        if (typeof a[this.props.sortedLabel] === "string") {

                            return a[this.props.sortedLabel].localeCompare(b[this.props.sortedLabel])
                        } else {
                            return a[this.props.sortedLabel] - b[this.props.sortedLabel]
                        }
                    }

                });
            } else {
                sortedData = [...this.state.filteredData].sort((a, b) => {
                    if (a !== null && b !== null) {
                        if (typeof a[this.props.sortedLabel] === "string") {

                            return b[this.props.sortedLabel].localeCompare(a[this.props.sortedLabel])
                        } else {
                            return b[this.props.sortedLabel] - a[this.props.sortedLabel]
                        }
                    }
                });
            }
            this.setState({
                columnNames: this.props.columnNames,
                sortType: this.props.sortType,
                filteredData: sortedData,
                sortedLabel: this.props.sortedLabel
            })

        }
    }
    sendData(data, dataName) {
        this.props.getData(data, dataName)
        
    }
    sendFilteredIndexes() {
        this.props.getFilteredIndexes(this.state.filteredIndexes)
    }
    handleChange = (e) => {
        const temp = [];
        this.state.filteredData.map((item, i) => {
            let count = 0
            for (var prop in item) {
                if (this.state.columnNames.some(column => prop === column)) {
                    if (item[prop].toString().toLowerCase().indexOf(e.target.value.toLowerCase()) > -1) {
                        count++
                    }
                }
            }
            if (count == 0) {
                temp[i] = null;
            } else {
                temp[i] = item;
            }
        })
        this.setState({
            [e.target.name]: e.target.value,
            newData: temp
        }, () => {   
            this.sendData(this.state.newData)

        })


    }


    filterColumn = (e) => {

        if (e.currentTarget.dataset.name !== this.state.activeColumn) {
            this.setState({
                toggle: true,
                activeColumn: e.currentTarget.dataset.name,         
            })
        } else {
            this.setState({
                toggle: false,
                activeColumn: 'lol',        
            })

        }
        const elements = []
        if (this.state.filterText !== '') {
            this.state.newData.map((elem, i) => {
                for (var prop in elem) {
                    if (prop === e.currentTarget.dataset.name) {
                        elements[i] = elem[prop];
                    }
                }
            })
        } else {
            this.state.filteredData.map((elem, i) => {
                for (var prop in elem) {
                    if (prop === e.currentTarget.dataset.name) {
                        elements[i] = elem[prop];
                    }
                }
            })
        }
        this.setState({
            filterColumnElements: elements,
        })
    }
    filterColumnElement = e => {
        const elements = []
        const { key } = e.currentTarget.dataset
        if (this.state.filterText !== '') {
            const temp = [...this.state.newData]
            const temp2 = [...this.state.filteredData]
            temp2[e.currentTarget.dataset.key] = null
            temp[e.currentTarget.dataset.key] = null

            this.setState({
                newData: temp,
                filteredData: temp2,




            }, () => {
                this.sendData(this.state.newData, 'newData')
                const elements = []
                if (this.state.filterText !== '') {
                    this.state.newData.map((elem, i) => {
                        for (var prop in elem) {
                            if (prop === this.state.activeColumn) {
                                elements[i] = elem[prop];
                            }
                        }
                    })
                } else {
                    this.state.filteredData.map((elem, i) => {
                        for (var prop in elem) {
                            if (prop === this.state.activeColumn) {
                                elements[i] = elem[prop];
                            }
                        }
                    })
                }
                this.setState({
                    filterColumnElements: elements,
                })

            })

        } else {
            const temp = [...this.state.filteredData]
            temp[key] = null

            this.setState({
                filteredData: temp,
            }, () => {

                this.sendData(this.state.filteredData, "filteredData")
                const elements = []
                if (this.state.filterText !== '') {
                    this.state.newData.map((elem, i) => {
                        for (var prop in elem) {
                            if (prop === this.state.activeColumn) {
                                elements[i] = elem[prop];
                            }
                        }
                    })
                } else {
                    this.state.filteredData.map((elem, i) => {
                        for (var prop in elem) {
                            if (prop === this.state.activeColumn) {
                                elements[i] = elem[prop];
                            }
                        }
                    })
                }
                this.setState({
                    filterColumnElements: elements,
                })
            })
        }
    }
    eraseFilter = () => {

        this.setState({
            filteredIndexes: [],
            filteredData: this.state.data,
            filterText: '',

        }, () => {
            this.sendData(this.state.filteredData, 'filteredData')
            const elements = []
            this.state.filteredData.map((elem, i) => {
                for (var prop in elem) {
                    if (prop === this.state.activeColumn) {
                        elements[i] = elem[prop];
                    }
                }
            })
            this.setState({
                filterColumnElements: elements
            })

        })

    }

    render() {
        return (
            <div className="filter-container row shadow bg-white rounded card px-4">
                <input type='text' name="filterText" value={this.state.filterText} onChange={this.handleChange.bind(this)} readOnly={false} placeholder="filter by keyword" />
                {this.state.columnNames.map((column, i) => {
                    if (column !== '') {
                        return <span className="first-span" key={i}> {this.state.actualString[i]}<i data-name={column} className="icofont-rounded-down icofont-1x first-icon" onClick={this.filterColumn} />
                            {(() => {
                                if (column === this.state.activeColumn) {
                                    return this.state.toggle && <div ref={this.setWrapperRef} className="filtered-results" style={{ zIndex: '99' }}>
                                        {this.state.filterColumnElements.map((elem, i) => {
                                            return <span className="second-span" key={i}> <i data-key={i} onClick={this.filterColumnElement} className="icofont-check-circled icofont-1x text-success" /> {elem}</span>
                                        })}
                                    </div>
                                }
                            })()}
                        </span>

                    }
                })}
                <span className="eraser-ni-eli-filter" onClick={this.eraseFilter}> erase filter </span>
            </div>

        )
    }
} 


