import React, { Component } from 'react';
export class Pagination extends Component {


    constructor(props) {
        const temp = []
        super(props);
        console.log(this.props.data)
        for (var i = 0, j = 0; i < this.props.perPage; j++) {
            if (this.props.data[i] !== null) {
                temp[j] = this.props.data[j]
                i++
            }
        }


        let allRows = document.getElementsByClassName('userInfo')

        this.state = {
            originalData: this.props.data,
            data: this.props.data,
            offset: 0,
            elements: this.props.data
                .slice(0, 0 + Number(this.props.perPage)),
            perPage: Number(this.props.perPage),
            currentPage: 0,
            pageCount: Math.ceil((this.props.data.length) / this.props.perPage),
            columnNames: this.props.columnNames,
            filteredIndexes: this.props.filteredIndexes,
            presentRows: allRows,
            checkedData: [],
            checkedBoxesIndex: [],
        }



    }
    componentDidUpdate(prevProps) {
        if (this.props.data !== prevProps.data || this.props.columnNames !== prevProps.columnNames || this.props.filteredIndexes
            !== prevProps.filteredIndexes) {
            let allRows = document.getElementsByClassName('userInfo')
            allRows = Array.from(allRows)
            var temp = []
            for (var i = 0, j = 0; j < this.props.data.length; j++) {
                if (this.props.data[j] !== null) {
                    temp[i] = this.props.data[j]
                    i++

                }
            }

            this.setState({
                originalData: this.props.data,
                data: temp,
                columnNames: this.props.columnNames,
                currentPage: 0,
                filteredIndexes: this.props.filteredIndexes,
                presentRows: allRows,
                pageCount: Math.ceil((i) / Number(this.state.perPage)),
                offset: 0,

            }, () => {
                this.setElementsForCurrentPage()
                this.sendData(this.state.checkedData, 'checkedData')
            });
        } else if (this.props.selectAll !== prevProps.selectAll) {
            console.log(this.props.selectAll)
            var tempBoxesIndex = [...this.state.checkedBoxesIndex]
            var checkedData = [...this.state.checkedData]

            if (this.props.selectAll) {
                tempBoxesIndex = []
                this.props.data.map((user, i) => {
                    tempBoxesIndex[i] = user[this.props.uniqueProperty].toString()
                    checkedData[i] = user
                })
            } else {
                tempBoxesIndex = []
                checkedData = []
            }
            this.setState({
                checkedBoxesIndex: tempBoxesIndex,
                checkedData: checkedData
            }, () => {
                this.setElementsForCurrentPage()
                console.log(this.state.checkedData)
                this.sendData(this.state.checkedData, 'checkedData')
            });


        }


    }

    setElementsForCurrentPage() {
        const elements = []
        for (var i = 0, j = this.state.offset; i < this.state.perPage && j < this.state.data.length; j++) {
            if (this.state.data[j] !== null) {
                elements[i] = this.state.data[j]
                i++
            }

        }
        this.setState({ elements: elements }, () => {
            var checkBoxes = document.getElementsByTagName("input")
            for (var i = 0; i < checkBoxes.length; i++) {
                if (checkBoxes[i].type === "checkbox" && checkBoxes[i].name!=='selectAll') {
                    var count = 0
                    this.state.checkedBoxesIndex.map((index) => {
                        if (checkBoxes[i].name === index) {
                            count++
                        }
                    })
                    if (count === 0) {
                        checkBoxes[i].checked = false
                    } else {
                        checkBoxes[i].checked = true
                    }
                }
            }
        })



    }
    handlePageClick = (data) => {
        if (data.target.id !== '0' && Number(data.target.id) <= this.state.pageCount) {
            const selectedPage = Number(data.target.id - 1);
            const offset = selectedPage * this.state.perPage;
            this.setState({ currentPage: selectedPage, offset: offset }, () => {
                this.setElementsForCurrentPage();
                console.log(this.state.currentPage)
            });
        }
    }


    handleChange = (e) => {
        const re = /^[0-9\b]+$/
        if (e.target.value === 0 || e.target.value === '' || e.target.value === '0') {
            this.setState({
                pageCount: 0,
                [e.target.name]: '0',
            }, () => {
                this.setElementsForCurrentPage()

            })
        } else if (re.test(e.target.value)) {
            this.setState({
                [e.target.name]: Number([e.target.value]),
                pageCount: Math.ceil((this.state.data.length) / Number([e.target.value])),
                currentPage: 0,
                offset: 0,
            }, () => {
                this.setElementsForCurrentPage()


            })
        }

    }
    handlePageClickPrev = (data) => {
        let offset = this.state.offset - (this.state.perPage * 10);
        let temp = 0
        if (offset < 1) {
            offset = 0;
        }
        if ((this.state.currentPage - 10) < 0) {
            temp = 0
        } else {
            temp = this.state.currentPage - 10
        }
        this.setState({ currentPage: temp, offset: offset }, () => {
            this.setElementsForCurrentPage();
        });
    }
    handlePageClickNext = (data) => {
        let offset = 10 * this.state.perPage + this.state.offset;
        let temp = 0
        if (offset >= (this.state.pageCount - 1) * this.state.perPage) {
            offset = Math.ceil((this.state.pageCount - 1) * this.state.perPage)
        }
        if ((this.state.currentPage + 10) >= this.state.pageCount) {
            temp = Math.ceil(this.state.pageCount - 1)
        } else {
            temp = Math.ceil(this.state.currentPage + 10)
        }
        this.setState({ currentPage: temp, offset: offset }, () => {
            this.setElementsForCurrentPage();

        });
    }

    handleChangeCheck = e => {
        console.log(e.target.name)
        var found = -1
        var tempData = []
        var elem = e.target.name
        this.state.checkedBoxesIndex.map((index, i) => {
            if (e.target.name === index) {
                console.log('sdklfj')
                found = i
            }
        })
        if (found !== -1) {
            console.log("hello")
            var temp = [...this.state.checkedBoxesIndex]
            temp.splice(found, 1)
            this.setState({
                checkedBoxesIndex: temp
            }, () => {
                this.state.checkedBoxesIndex.map((index, i) => {
                    this.state.originalData.map((user) => {
                        if (user[this.props.uniqueProperty] === Number(index)) {
                            tempData[i] = user
                        }
                    })
                })
                this.setState({
                    checkedData: tempData
                }, () => {
                    this.sendData(this.state.checkedData, 'checkedData')
                    
                })
            })
        } else {


            this.setState({
                checkedBoxesIndex: [...this.state.checkedBoxesIndex, e.target.name]
            }, () => {
                var j = 0
                this.state.checkedBoxesIndex.map((index, i) => {

                    this.state.originalData.map((user) => {
                        if (user[this.props.uniqueProperty] === Number(index) && user !== null) {
                            tempData[j] = user
                            j++
                        }
                    })
                })
                this.setState({
                    checkedData: tempData
                }, () => {
                    this.sendData(this.state.checkedData, 'checkedData')
                })
            })
        }
    }
    sendData = (data, dataName) => {
        this.props.getData(data, dataName)
    }
    render() {
        let paginationElement;
        const pageNumbers = [];
        var i = ((this.state.currentPage) - ((this.state.currentPage) % 10)) + 1;
        for (var j = 0; j <= 9 && i <= this.state.pageCount; i++ , j++) {
            pageNumbers.push(i);
        }
        

        paginationElement = (
            <div className="custom-pagination row">
                <i onClick={this.handlePageClickPrev} className="icofont-rounded-double-left icofont-2x" />
                <div className="number-page-wrapper">
                    <i onClick={this.handlePageClick} id={this.state.currentPage} className="icofont-rounded-left icofont-2x" />
                {pageNumbers.map(number => {
                    return <li className={((this.state.currentPage+1)===number ? 'text-danger' : '')} key={number} id={number}  onClick={this.handlePageClick}>
                        {number}
                    </li>
                    })}

                    <span className="custom-dotted" id={((this.state.currentPage) - ((this.state.currentPage) % 10)) + 11} onClick={this.handlePageClick} style={{ display: i > this.state.pageCount ? 'none' : 'inline-flex' }}>
                       ...
                    </span>
                    <i onClick={this.handlePageClick} id={this.state.currentPage + 2} className="icofont-rounded-right icofont-2x" />
                </div>
                <i  onClick={this.handlePageClickNext} className="icofont-rounded-double-right icofont-2x" />
            </div>


        );      

        const { data, elements, columnNames } = this.state;
        return (
            <div className="pagination-row">
                {elements.map((user, i) => {
                    return <div className="userInfo row" onClick={this.showUserDetails} data-id={user[this.props.uniqueProperty]} key={i}>
                        <input type="checkbox" onChange={this.handleChangeCheck} name={user[this.props.uniqueProperty]} />
                        {Object.keys(user).map(function (key, i) {
                            {
                                return columnNames.map(function (column, i) {

                                    if (column === key) {
                                        return <div style={{maxWidth: 'initial'}} className="col"><span key={i} style={{ width: '100%' }}> {user[key]}</span></div>;
                                    }
                                })
                            }
                        })}
                    </div>

                })}
                <div className="rows-per-display-handler row mt-6">
                    <div className="col-sm-6 lh-60">
                        <label> Rows per display: </label> <input type='text' name="perPage" value={this.state.perPage} onChange={this.handleChange.bind(this)} readOnly={false} />
                    </div>
                    <div className="col-sm-6 lh-60 ml-auto">
                        {paginationElement}
                    </div>

                </div>
            </div>

        )
    }
}



