import React, { Component } from 'react';
import { Pagination } from './Pagination';
import { Filter } from './Filter';
import AddContact from '../contacts/AddContact'
import { deleteContact } from '../../actions/contactActions';
import { addContact } from '../../actions/contactActions';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
class DataGrid extends Component {
    
    constructor(props) {
        super(props);
        var strings = this.props.columnNames.split(',')
        var initialString = []
        var actualString = []
        strings.map((string, i) => {
            actualString[i] = string.substr(string.indexOf("=") + 1, string.length)
            initialString[i] = string.substr(0, string.indexOf("="))
        })
        console.log(actualString, this.props.data)
        console.log(strings)
        this.state = {
            data: this.props.data,
            columnNames: initialString,
            showColumnNames: initialString,
            dataPaginate: this.props.data,
            addClass: false,
            hide: true,
            filterShowHide: false,
            filterColumnElements: [],
            filteredIndexes: [],
            sortedData: [],
            prevSortedColumn: '',
            sortType: this.props.sortType,
            sortedLabel: this.props.sortedColumn,
            checkedData: [],
            selectAll: false,
            actualString: actualString,
        };
        this.setWrapperRef = this.setWrapperRef.bind(this);
        this.handleClickOutside = this.handleClickOutside.bind(this);
        
    }
    componentDidMount() {
        document.addEventListener('mousedown', this.handleClickOutside);
    }
    componentDidUpdate(prevProps) {
        if (this.props.data !== prevProps.data) {
            this.setState({
                data: this.props.data,
                dataPaginate: this.props.data
            })
            console.log(this.props.data)
        }
    }

    componentWillUnmount() {
        document.removeEventListener('mousedown', this.handleClickOutside);
    }
    setWrapperRef(node) {
        this.wrapperRef = node;
    }
    handleClickOutside(event) {
        if (this.wrapperRef && !this.wrapperRef.contains(event.target)) {
            this.setState({
                addClass: false,
                hide: true,
            })
        }
    }

    getDataFromFilter(data, dataName) {
        let temp = []
        for (var i = 0, j = 0; j < data.length; j++) {
            if (data[j] !== null) {
                temp[i] = data[j]
                i++

            }
        }
        this.setState({
            dataPaginate: temp,
        }, () => {
        })
    }
    getDataFromPagination(data, dataName) {
        if (dataName === 'checkedData') {
            this.setState({
                checkedData: data
            }, () => {
            })
        }
    }
    getFilteredIndexes(data) {
        this.setState({
            filteredIndexes: data,
        }, () => {

        })
    }
    handleClick = (e) => {
        var array = [...this.state.showColumnNames]


        if (e.currentTarget.className.includes('text-success')) {
            e.currentTarget.className = e.currentTarget.className.replace('text-success', '');
            this.state.columnNames.map(function (key, i) {
                if (key === e.currentTarget.dataset.key) {
                    array[i] = ''
                }
            })
            this.setState({
                showColumnNames: array,


            })
        } else {
            e.currentTarget.className += " text-success"
            this.state.columnNames.map(function (key, i) {
                if (key === e.currentTarget.dataset.key) {
                    array[i] = key;
                }
            })
            this.setState({
                showColumnNames: array,


            })
        }

    }
    showAllCol() {
        const { handleClick } = this
        this.setState({
            showColumnNames: this.state.columnNames,
        })
        let eyeIcons = document.getElementsByClassName('icofont-eye-alt icofont-3x')
        eyeIcons = Array.from(eyeIcons);

        {
            eyeIcons.map(function (icon, i) {
                icon.className = 'filter icofont-eye-alt icofont-3x text-success'
            })
        }
    }
    toggleFilter = () => {
        this.setState({
            filterShowHide: !this.state.filterShowHide
        })
    }
    toggleShowHide = () => {
        var elem = document.getElementsByClassName('column-list')
        this.wrapperRef = elem[0]
        this.setState({
            addClass: !this.state.addClass,
        })

    }
    toggleHide = (e) => {
        var elem = document.getElementsByClassName('parentClass')
        this.wrapperRef = elem[0]
        console.log(elem)
        this.setState({
            hide: !this.state.hide
        })

    }
    sortData = (e) => {

        this.setState({
            isSorted: !this.state.isSorted,
            sortedLabel: e.currentTarget.dataset.label,
        })
        var sortedColumn = document.getElementById(e.currentTarget.dataset.label)
        let sortedData = []
        if (this.state.prevSortedColumn === '') {
            sortedColumn.className += ' icofont-arrow-up icofont-2x'
            sortedData = [...this.state.dataPaginate].sort((a, b) => {
                if (typeof a[e.currentTarget.dataset.label] === "string") {
                    return a[e.currentTarget.dataset.label].localeCompare(b[e.currentTarget.dataset.label])
                } else {
                    return a[e.currentTarget.dataset.label] - b[e.currentTarget.dataset.label]
                }
            });
            this.setState({
                sortType: 'ascending'
            })
        } else if (sortedColumn === this.state.prevSortedColumn) {

            if (this.state.prevSortedColumn.className.includes('-up')) {

                sortedData = [...this.state.dataPaginate].sort((a, b) => {
                    if (typeof a[e.currentTarget.dataset.label] === "string") {
                        return b[e.currentTarget.dataset.label].localeCompare(a[e.currentTarget.dataset.label])
                    } else {
                        return b[e.currentTarget.dataset.label] - a[e.currentTarget.dataset.label]
                    }
                });
                var temp = this.state.prevSortedColumn

                temp.className = temp.className.replace(' icofont-arrow-up icofont-2x', ' icofont-arrow-down icofont-2x')

                this.setState({
                    prevSortedColumn: temp,
                    sortType: 'descending',
                })

            } else {

                sortedData = [...this.state.dataPaginate].sort((a, b) => {
                    if (typeof a[e.currentTarget.dataset.label] === "string") {
                        return a[e.currentTarget.dataset.label].localeCompare(b[e.currentTarget.dataset.label])
                    } else {
                        return a[e.currentTarget.dataset.label] - b[e.currentTarget.dataset.label]
                    }
                });
                this.state.prevSortedColumn.className = this.state.prevSortedColumn.className.replace(' icofont-arrow-down icofont-2x', ' icofont-arrow-up icofont-2x')
                this.setState({
                    sortType: 'ascending'
                })
            }
        } else {

            if (this.state.prevSortedColumn.className.includes('up')) {
                this.state.prevSortedColumn.className = this.state.prevSortedColumn.className.replace('icofont-arrow-up icofont-2x', '');
            } else {
                this.state.prevSortedColumn.className = this.state.prevSortedColumn.className.replace('icofont-arrow-down icofont-2x', '');
            }
            sortedColumn.className += ' icofont-arrow-up icofont-2x'


            sortedData = [...this.state.dataPaginate].sort((a, b) => {
                if (typeof a[e.currentTarget.dataset.label] === "string") {
                    return a[e.currentTarget.dataset.label].localeCompare(b[e.currentTarget.dataset.label])
                } else {
                    return a[e.currentTarget.dataset.label] - b[e.currentTarget.dataset.label]
                }
            });
            this.setState({
                sortType: 'ascending'
            })
        }
        this.setState({
            prevSortedColumn: sortedColumn,
            dataPaginate: sortedData,
        })


    }
    handleChangeCheck = e => {
        if (e.target.checked) {
            console.log('hi')
            this.setState({
                selectAll: true
            })
        } else {     
            this.setState({
                selectAll: false
            })
        }
    }
    deleteUser = e => {
        var del = [...this.state.checkedData]
        del.map(del => {
            this.props.deleteContact(del.id)
        })
    }
    render() {
        console.log(this.state.checkedData, this.state.data)
        const { data, elements, isSorted, actualString } = this.state;
        const { handleClick, showAllCol, sortData } = this
        return (
            
            <div className="userContainer">
                <div className="userListContainer">
                  
                    <div className="userListHeader row" style={{ zIndex: '99' }}>
                        {/*USER  */}
                        <div className={this.state.hide
                            ? 'parentClass hides'
                            : 'parentClass show'
                        }>

                            <div onClick={this.toggleHide} className="addUser" style={{ cursor: "pointer" }} >
                            <i  className="icofont-contact-add icofont-2x" />
                            <span style={{ height: '100%' }}> New user </span>
                        </div>
                      
                            <AddContact />

                        </div>
                        
                        <div
                            className={
                                this.state.filterShowHide
                                    ? "col-sm-10 pull-left filterShow"
                                    : "col-sm-10 pull-left filterHide"
                            }
                        >
                            {/*  FILTER */}
                            {this.state.data.length !== 0 ? <Filter actualString={this.state.actualString} className="row col-sm-12" sortedLabel={this.state.sortedLabel} sortType={this.state.sortType} getFilteredIndexes={this.getFilteredIndexes.bind(this)} columnNames={this.state.showColumnNames} getData={this.getDataFromFilter.bind(this)} data={this.state.data} /> : null}

                            <div className="icons-controller">
                                <i onClick={this.toggleFilter} className="filter icofont-filter icofont-2x" />
                               <i onClick={this.toggleShowHide} className="filter icofont-eye-alt icofont-2x" />
                            </div>
                        </div>
                    </div>
                    <div className="userListLabel row bg-primary responsive-data-row">
                        <input type="checkbox" checked={this.state.selectAll} name="selectAll" onChange={this.handleChangeCheck} />
                        {this.state.showColumnNames.map(function (key, i) {

                            if (key !== '') {
                                return <div style={{maxWidth: 'initial'}} className="col align-item-center" data-label={key} onClick={sortData} key={i}><span key={i} style={{ width: '100%', color: "#fff" }}> {actualString[i]}<i id={key} /></span></div>;
                            }
                        })}
                    </div>
                    {/*  PAGINATION */}
                    {this.state.data.length !== 0 ? <Pagination selectAll={this.state.selectAll} filteredIndexes={this.state.filteredIndexes} columnNames={this.state.showColumnNames} data={this.state.dataPaginate} perPage={this.props.perPage} getData={this.getDataFromPagination.bind(this)} uniqueProperty={this.props.uniqueProperty} /> : null}
                   

                </div>
                {/*Column Show*/}

                <div className={this.state.addClass ? "column-list shadow bg-white rounded card col-sm-3 row p-3 show" : "column-list shadow bg-white rounded card row col-sm-3 p-3 hide"}>
                    <span><i className="filter icofont-eye-alt icofont-3x " onClick={showAllCol.bind(this)} /> COLUMNS</span>
                    {this.state.columnNames.map(function (key, i) {
                        return <span key={i}><i data-key={key} className="filter icofont-eye-alt icofont-3x text-success" onClick={handleClick.bind(this)} />{actualString[i]}</span>;
                    })}
                  
                </div>
                <button className="deleteUser text-white bg-danger px-4 py-2 hoverable" onClick={this.deleteUser}> DELETE </button>
              
            </div>

        )
    }
}

export default connect(
    null,
    { deleteContact },
)(DataGrid);



