import {
    AUTHENTICATE_USER,LOGOUT_USER
} from '../actions/types';

const initialState = {
    requestData: '',
    isFetching: true,
    timeOutz: false,
    authenticated:false,
    logoutTime: 100000*9,
    warningTime: 100000*9,
    logoutMessage:''
};

export default function (state = initialState, action) {
    
    switch (action.type) {
        case AUTHENTICATE_USER:
            return {
                ...state,
                requestData: action.payload, isFetching: false, authenticated: true
            };
        case LOGOUT_USER:
            return {
                ...state, requestData:'',
                logoutMessage: action.payload, timeOutz: true, authenticated:false
            };
        default:
            return state;
    }
}



