import React, { Component } from 'react';
import { MyPreferences } from './settings-child-menu/MyPreferences';
import { OtherMenu } from './settings-child-menu/OtherMenu';
import ContentComingSoon from '../../components/ContentComingSoon';

export class Settings extends Component {
    constructor(props) {
        super(props);
        this.state = {
            key: null,
            components: [
                { id: 1, componentClass: "icofont-dashboard icofont-3x", name: "my preferences", compoName: "MyPreferences" },
                { id: 2, componentClass: "icofont-inbox icofont-3x", name: "other menu", compoName: "OtherMenu" },
            ],
            activeID: 1,
            componentTitle: '',
            isHideTitle: true,
            isHideee: this.props.isHideee,
            isHide: true,
        };
        this.getData = this.getData.bind(this);
    }


    componentDidUpdate(prevProps) {
        if (this.props.isHideee !== prevProps.isHideee) {
            this.setState({
                isHideee: this.props.isHideee,
                isHide: !this.state.isHide,
            })
            console.log(this.state.isHideee)
        }
    }
    sendIsHideData = () => {
        this.props.getIsHideData(this.state.isHideee)
    }
    handleClick = (e) => {
        console.log(e.target.getAttribute('name'))
        if (e.target.getAttribute('name') === 'my preferences') {
            this.setState({
                key: <MyPreferences />, componentTitle: 'My Preferences', isHideee: true, isHide:true

            }, () => {
                this.sendIsHideData
            })
        } else if (e.target.getAttribute('name') === 'other menu') {
            this.setState({
                key: <OtherMenu />, componentTitle: 'Other Menu', isHideee: true,isHide: true

            })
        }
        const id = parseInt(e.target.id);
        this.setState({
            activeID: id
        });
        console.log(this.state.activeID)
    }
    getData(val, title) {
        this.setState({
            key: val,
            compoName: title
        });
    }
    render() {
        console.log(this.state.isHideee)
        const { key } = this.state;
        return (
            <div
                className="col-sm-12 text-center">
                <ContentComingSoon />         

                <h4 className={this.state.componentTitle}>
                    {this.state.componentTitle}
                </h4>

                <div className="row">
                    {key}
                    </div>
                <ul
                    className={
                        this.state.isHideee 
                            ? "settings-nav hide"
                            : "settings-nav"
                    }
                >
                    {this.state.components.map((c, i) => {
                        return <UnderSettings active={c.id === this.state.activeID} id={c.id} onClick={this.handleClick.bind(this)} componentClass={c.componentClass} name={c.name} compoName={c.compoName} key={i} />
                    })}
                </ul>
               
            </div>
        )
    }
}


class UnderSettings extends React.Component {
    render() {
        return <li className={"item" + (this.props.active ? ' active ' : '')} id={this.props.id} onClick={this.props.onClick}>
            <i className={this.props.componentClass} name={this.props.name} id={this.props.id}> </i> <span name={this.props.name} id={this.props.id}>{this.props.compoName}</span>
        </li>
    }
}




